<template>
    <div class="Number" v-show="display">
        <p>{{number}}</p>
    </div>
</template>
<script>
export default {
    name:'Number',
    props:{
        number:Number
    },
    data(){
        return{
            display:true
        }
    },
    mounted(){
        this.test();
    },
    methods:{
        test:function(){
            if(this.number==0){
                this.display=false;
            }
        }
    }
}
</script>
<style scoped>
.Number
{
    background-color:#BF544A;
    width:15pt;
    height:15pt;
    border-radius:50%;
    font-size:11pt;
    display:flex;
    justify-content:center;
    align-items:center;
    color:white;
    font-family:'Times New Roman', Times, serif;
    font-weight:bold;
}
</style>